.featured{
    background-color: black;
    width:100%;
    height:90vh;
    
 

}
.category{
    position: absolute;
    top:100px;
    display: flex;
    margin-left:20px;
    
}
#genre{
    margin-left: 10px;
    
}

.info{
    display: flex;
   
    flex-direction: column;
    align-items: center;
    padding:5px;
    width:300px;
    margin-left:20px;
    position:absolute ;
    top: 300px;

}
.buttons{
    display: flex;
    
    
    justify-content: space-between;
    
}
button{
    display:flex;
    justify-content: center;
    align-items: center;
    margin-left:10px;
    width:100px;
    border-radius: 15px;
    border:0px ;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
    
}