.register {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
      to bottom,
      rgba(27, 26, 26, 0) 0%,
      rgba(0, 0, 0, 1) 100%
    ),
    url("./coverpage.jpg");
  background-size: cover;

  position: relative;
}

.wrapper {
  padding: 20px 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  height: 40px;
}

.logButton {
  background-color: red;
  width: 100px;
  border: none;
  color: white;
  border-radius: 5px;
  padding: 5px 15px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}
.logButton:hover {
  background-color: green;
}

.rcontainer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.rcontainer > h1 {
  font-size: 50px;
}

.rcontainer > h2 {
  margin: 20px;
}

.rcontainer > p {
  font-size: 20px;
}

.rcontainer > .input {
  width: 50%;
  /* background-color: red; */
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
  height: 50px;
  border-radius: 5px;
}

input {
  background-color: none;
  margin:5px;
  flex: 9;
  height: 100%;
  /* border: none; */
  padding: 0 10px;
}

.rregisterButton {
  flex: 3;
  height: 100%;
  background-color: red;
  border-radius: none;
  color: white;
  font-size: 18px;
  cursor: pointer;
}
